	module.exports = {"data":{"markdownRemark":{"html":"<p>I didn’t get a chance to write up my experience with the last couple of days of learning dev camp. So today, let’s talk today…</p>\n<h2>Day 2</h2>\n<p>The second day was probably by favorite. I started with a 7:30 (way too early in the morning), with a informal conversation about consulting businesses with Ray Jimenez and other business owners (or hopefuls). This was useful, because one of the things I’ve been trying to think about is how to focus my career as a freelancer. Mostly, what I learned was to stay away from the noise in our industry and to focus on deep relationships. Like I mentioned last, time, one of the main things I took away from several sessions was that I need to dedicate some time to work on my resources and my business plan and revamp my portfolio. After that, it was on to morning sessions.</p>\n<h3>Morning Sessions</h3>\n<p>I spent most of my morning in a session on getting started with VR with <a href=\"http://LinkedIn.com/in/myraroldan\">Myra Roldon</a>. It was amazing! Honestly, probably my favorite session. As you may have read in my previous write ups was that I was left with a ton of questions, especially around when to use VR (and to a lesser extent AR). Myra offered an amazing framework for this, specifically, D.R.E.D which stands for:</p>\n<ul>\n<li><strong>Dangerous:</strong> Those tasks which would be super dangerous to drop a learner into without some initial training</li>\n<li><strong>Rare:</strong> Experiences that most people would not be able to have</li>\n<li><strong>Expensive:</strong> Experiences that would cost a great deal of money</li>\n<li><strong>Difficult:</strong> Experiences that require a lot of logistical pieces that are difficult to accomplish</li>\n</ul>\n<p>We focused on some great free tools for prototyping (or just plain creating) some simpler WebVR experiences. Myra livecast the entire session, so check it out! (don’t mind me getting up to plug in my rapidly dying laptop)\n<div>\n          <div\n            class=\"gatsby-resp-iframe-wrapper\"\n            style=\"padding-bottom: 56.49999999999999%; position: relative; height: 0; overflow: hidden;\"\n          >\n            <iframe src=\"https://www.youtube.com/embed/1lagZpgoiyw?rel=0\" class=\"embedVideoIframe\" allowfullscreen style=\"\n            position: absolute;\n            top: 0;\n            left: 0;\n            width: 100%;\n            height: 100%;\n          \"></iframe>\n      <style>\n        .embedVideoIframe {\n          border: 0\n        }\n      </style>\n          </div>\n          </div></p>\n<h3>Afternoon Sessions</h3>\n<p>My first session of the afternoon was on reflecting on the way comic books use visual communication tools and how we might use those techniques with Michael Sheyahshe. For the most part, having read a lot on UX and visual design and other design competencies, I had heard several of the considerations, but the ones that stuck out to me were:</p>\n<ul>\n<li><strong>Panels:</strong> The use of panels not only for having visual boundaries, but for indicating time and dynamic movement. It made me think about layout in a different way</li>\n<li><strong>Reader UX:</strong> Digital comic viewers like Comixology have created a user experience such that the reader can enjoy the whole page OR experience a guided panel-to-panel experience that allows them to reveal each panel individually. I liked both the idea of giving the reader a choice on how to experience and the idea of the guided reveal as a way to help tell the story.</li>\n<li><strong>Infinite Canvas:</strong> The thing that most captured my imagination was the idea about putting some things off screen and considering an infinite canvas as opposed to being limited to the screen size. I’m not quite sure what I would do with that during design, but I’m intrigued about what that might mean.</li>\n</ul>\n<figure>\n    \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 810px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"slide about history of comic book communcation\" title=\"\" src=\"/static/img_2235-2a6a3997bdb1f65eec810ea103780902-aabb5.jpg\" srcset=\"/static/img_2235-2a6a3997bdb1f65eec810ea103780902-2320f.jpg 203w,\n/static/img_2235-2a6a3997bdb1f65eec810ea103780902-427c1.jpg 405w,\n/static/img_2235-2a6a3997bdb1f65eec810ea103780902-aabb5.jpg 810w,\n/static/img_2235-2a6a3997bdb1f65eec810ea103780902-f0fe6.jpg 1215w,\n/static/img_2235-2a6a3997bdb1f65eec810ea103780902-c6859.jpg 1620w,\n/static/img_2235-2a6a3997bdb1f65eec810ea103780902-648e9.jpg 2430w,\n/static/img_2235-2a6a3997bdb1f65eec810ea103780902-0e50d.jpg 4032w\" sizes=\"(max-width: 810px) 100vw, 810px\">\n    </span>\n  </span>\n  \n    <figcaption>Comic book visual communication</figcaption>\n</figure>\n<p>My second session was also with Ray Jimenez and focused on deep diving with learners. The one thing that stuck to me was the idea that:</p>\n<blockquote>\n<p>Engagement is having a problem to solve.</p>\n<p>– Ray Jimenez</p>\n</blockquote>\n<p>So often when I hear about engagement, it’s in terms of gamification or prettier courses or game-based courses or something like that. I’m not against game-based learning or good visual design, but engagement doesn’t have to come from trying to make something fun. When I need to learn something new because I’ve got a problem to solve, whether or not I’m having fun, I’m super motivated and engaged. I know I love development work, but thinking about this in terms of how I can help to advocate for helping the learner solve problems that they actually come up against, no matter what the mode (thinking back to the learning journeys session) is really the key. So this was really powerful.</p>\n<h2>Day 3</h2>\n<p>Today was a half day. I spent the morning in Jason Bickle’s session around pragmatic instructional design. In his DECLARE method, I saw a lot of what I’ve done previously with clients by asking them to help me plan out, in spreadsheet form, the content for the course and the correct medium, and the necessary assets. This was really more of a form of project and asset management for efficiency, and it was interesting to hear about how Jason has used his method to make development work work better.</p>\n<figure>\n    \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 810px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"stages of conflict journey\" title=\"\" src=\"/static/img_2241-c8d99bb4bb383fb57ca1c690fbe379ab-aabb5.jpg\" srcset=\"/static/img_2241-c8d99bb4bb383fb57ca1c690fbe379ab-2320f.jpg 203w,\n/static/img_2241-c8d99bb4bb383fb57ca1c690fbe379ab-427c1.jpg 405w,\n/static/img_2241-c8d99bb4bb383fb57ca1c690fbe379ab-aabb5.jpg 810w,\n/static/img_2241-c8d99bb4bb383fb57ca1c690fbe379ab-f0fe6.jpg 1215w,\n/static/img_2241-c8d99bb4bb383fb57ca1c690fbe379ab-c6859.jpg 1620w,\n/static/img_2241-c8d99bb4bb383fb57ca1c690fbe379ab-648e9.jpg 2430w,\n/static/img_2241-c8d99bb4bb383fb57ca1c690fbe379ab-0e50d.jpg 4032w\" sizes=\"(max-width: 810px) 100vw, 810px\">\n    </span>\n  </span>\n  \n    <figcaption>8 stages of the conflict journey</figcaption>\n</figure>\n<p>Then in Katie Stroud’s session, she overlayed the hero’s journey to thinking about how do we design around the conflict journey for change management for a user. I think really insightful steps were thinking about:</p>\n<ul>\n<li><strong>3 Resistance:</strong> Are we trying to deploy training before learners have reached the stage of conflict where they recognize that there is a problem to solve? If so, we may be causing conflict.</li>\n<li><strong>6 Moving into the Unknown:</strong> What is going to get in the way of learners being able to implement the changes and how do we design around that?</li>\n</ul>\n<p>My last presenter session was from Joseph Suarez (!!!) where he told stories about his experience as an instructional designer and extrapolated some important lessons learned. I’ve read quite a lot around UX (user experience) and Joe shared a really impactful story around how observing his learners with training he designed really blew a whole in his thinking about how he advocated for design. While companies will often prioritize UX in other areas, I think they don’t expect it from us, but those tools and methodologies are essential for the work that we do, as well. As I’ve said before, I think there’s a TON we can learn from that discipline.</p>\n<figure>\n    \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 810px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"gap between web development and elearning development\" title=\"\" src=\"/static/img_2244-687afbcea5ea70b2bcaf3efe9a435367-aabb5.jpg\" srcset=\"/static/img_2244-687afbcea5ea70b2bcaf3efe9a435367-2320f.jpg 203w,\n/static/img_2244-687afbcea5ea70b2bcaf3efe9a435367-427c1.jpg 405w,\n/static/img_2244-687afbcea5ea70b2bcaf3efe9a435367-aabb5.jpg 810w,\n/static/img_2244-687afbcea5ea70b2bcaf3efe9a435367-f0fe6.jpg 1215w,\n/static/img_2244-687afbcea5ea70b2bcaf3efe9a435367-c6859.jpg 1620w,\n/static/img_2244-687afbcea5ea70b2bcaf3efe9a435367-648e9.jpg 2430w,\n/static/img_2244-687afbcea5ea70b2bcaf3efe9a435367-0e50d.jpg 4032w\" sizes=\"(max-width: 810px) 100vw, 810px\">\n    </span>\n  </span>\n  \n    <figcaption>The strangeness of elearning courses comes from not bridging the gap.</figcaption>\n</figure>\n<h2>My Experience</h2>\n<p>On the whole, my experience with Learning Dev Camp has been pretty good. My first day or two were a bit spotty. I had a lot of questions (which is probably a good thing) but I wasn’t feeling inspired or excited or any of those things that other people say they feel when they come away from conferences. I still wouldn’t say I was either of those things, but I’ve heard a lot of good sessions and I’m coming away with a need to take some time to reflect on the way I work and my processes.</p>","frontmatter":{"date":"June 15, 2018","path":"/blog/weeknotes-learning-dev-camp-day-2-to-3","title":"Weeknotes: Learning Dev Camp Day 2 to 3","tags":["conferences","elearning","learning dev camp"],"categories":["Commonplace Book","Weeknotes"],"featured":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":1.7777777777777777,"src":"/static/Learning-Dev-Camp-Day-2-and-3-0f92e8e9c4a9351beb8a231659ecd754-c9de9.png","srcSet":"/static/Learning-Dev-Camp-Day-2-and-3-0f92e8e9c4a9351beb8a231659ecd754-aafd1.png 203w,\n/static/Learning-Dev-Camp-Day-2-and-3-0f92e8e9c4a9351beb8a231659ecd754-c6a3b.png 405w,\n/static/Learning-Dev-Camp-Day-2-and-3-0f92e8e9c4a9351beb8a231659ecd754-c9de9.png 560w","sizes":"(max-width: 560px) 100vw, 560px"}}}}}},"pathContext":{}}

